let configs = defineConfigs({
  node: {
    selectable: 2, // up to 2 nodes
    focusring: {
      color: "darkgray",
    },
  },
  edge: {
    selectable: true,
    normal: {
      width: 3,
      dasharray: edge => (edge.dashed ? "4" : "0"),
    },
    selected: {
      color: "darkgray",
      dasharray: edge => (edge.dashed ? "4" : "0"),
    }
  },
  path: {
    visible: true,
    normal: {
      width: path => (path.canSee ? 10 : 0),
      color: p => p.color,
    },
  },
})

export default {
  nodes,
  edges,
  layouts,
  configs,
  paths,
}